body {
  font-family: "Cormorant Upright", serif;
  background: #E9F4E9;
  padding-top: 68px;
  color: #202027; }

a {
  font-size: 1.5rem;
  text-decoration: none;
  color: #02300c; }
  a a:hover {
    color: #4b0b0b; }

img {
  max-width: 100%; }

.ux-message {
  margin: 100px auto;
  text-align: center;
  width: 60vw;
  font-size: 3rem;
  padding: 15px;
  background: #570303;
  border-radius: 10px;
  color: #FAFFFA; }

.web-header {
  position: fixed;
  width: 100vw;
  z-index: 99999;
  top: 0;
  padding: 15px;
  background: #E9F4E9; }
  .web-header .web-title {
    font-size: 2rem;
    font-family: "Junge", serif;
    width: 100%;
    min-width: 260px;
    padding-left: 15px;
    text-align: left;
    margin: 0 auto;
    color: #570303; }
  .web-header .fa-bars {
    position: absolute;
    top: 17px;
    right: 5%;
    color: #363646;
    font-size: 2rem;
    padding: 0 15px; }
  .web-header .header-components {
    display: none; }
  @media only screen and (min-width: 320px) {
    .web-header .web-title {
      text-align: center; } }
  @media only screen and (min-width: 750px) {
    .web-header .fa-bars {
      display: none; }
    .web-header .web-title {
      text-align: center;
      font-size: 4rem; }
    .web-header .header-components {
      display: flex; }
    .web-header > a {
      position: absolute;
      top: 30px;
      right: 160px; }
    .web-header #search-input {
      position: absolute;
      color: #B9C1BC;
      top: 30px;
      font-size: 1.5rem;
      right: 25px;
      width: 120px;
      cursor: pointer;
      border: 0 solid;
      background: #E9F4E9; }
    .web-header ul {
      margin: 0 auto;
      padding: 0;
      padding-top: 15px; }
    .web-header .categories-list li {
      display: inline-block;
      padding: .5rem 1rem; }
      .web-header .categories-list li a:hover {
        color: #4b0b0b; } }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }
  .web-header.show-menu .header-components {
    animation: fadeIn 1s;
    display: flex;
    flex-direction: column; }
    .web-header.show-menu .header-components ul {
      margin: 0;
      padding: 0; }
      .web-header.show-menu .header-components ul li {
        list-style-type: none;
        padding: 15px;
        text-align: center; }
        .web-header.show-menu .header-components ul li a:hover {
          color: #4b0b0b; }
  .web-header.show-menu #sign-in-link {
    text-align: center;
    margin-top: 5px;
    color: #02300c; }
  .web-header.show-menu #search-input {
    margin: 15px auto;
    cursor: pointer;
    border: 0 solid;
    font-size: 1.5rem;
    background: #E9F4E9;
    text-align: center;
    color: #202027; }

.web-footer {
  display: flex;
  padding: 15px;
  width: 100vw;
  align-items: center;
  justify-content: space-around;
  bottom: 0;
  font-size: 0.8rem;
  background: #E9F4E9; }
  .web-footer .footer-element {
    color: #570303; }
  @media only screen and (min-width: 520px) {
    .web-footer {
      font-size: 1.5rem; } }

.articles-list .articles {
  display: grid;
  align-items: center;
  border-bottom: 1px solid #B9C1BC;
  grid-template-columns: 70px auto;
  padding: 0 15px; }
  .articles-list .articles .cover {
    width: 70px;
    height: 70px; }
    .articles-list .articles .cover .article-cover {
      min-width: 140px; }
  .articles-list .articles:hover {
    box-shadow: 0 0 5px #B9C1BC;
    background: #eff1f0; }
  .articles-list .articles:active {
    box-shadow: 0 0 1px #B9C1BC; }
  .articles-list .articles .cover {
    align-self: center;
    justify-self: center;
    position: relative;
    display: inline-block;
    border-radius: 10px;
    overflow: hidden; }
    .articles-list .articles .cover .article-cover {
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); }
  .articles-list .articles .info {
    padding: 15px 15px;
    overflow: hidden; }
    .articles-list .articles .info a > .title {
      margin: 0;
      margin-bottom: 5px;
      line-height: 1;
      font-size: 1.5rem; }
    .articles-list .articles .info .published-date {
      font-size: 0.8rem;
      line-height: 0.8;
      margin: 0; }
    .articles-list .articles .info .hide-elements {
      display: none; }

@media only screen and (min-width: 520px) {
  .articles-list {
    width: 90%;
    margin: 0 auto; }
    .articles-list .articles {
      grid-template-columns: 150px auto;
      padding: 0 15px; }
      .articles-list .articles .cover {
        width: 150px;
        height: 150px; }
        .articles-list .articles .cover .article-cover {
          min-width: 300px; }
      .articles-list .articles .info .hide-elements {
        display: block; }
      .articles-list .articles .info .author-wrapper {
        display: flex;
        height: 40px;
        align-items: center; }
        .articles-list .articles .info .author-wrapper .profile-picture {
          width: 30px;
          height: 30px;
          border-radius: 50%; }
        .articles-list .articles .info .author-wrapper .author {
          font-size: 1rem;
          padding: 15px; }
      .articles-list .articles .info .text {
        margin-bottom: 30px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden; }
      .articles-list .articles .info .bottom-info-wrapper {
        display: grid;
        justify-self: stretch;
        align-items: center;
        grid-template-columns: 3fr 3fr 1fr;
        grid-gap: 10px;
        font-size: 1.5rem; }
        .articles-list .articles .info .bottom-info-wrapper .comments {
          font-size: 1.5rem;
          justify-self: end; } }

@media only screen and (min-width: 750px) {
  .articles-list {
    margin-top: 100px;
    width: 80%; }
    .articles-list .articles {
      grid-template-columns: 300px auto;
      padding: 0 15px; }
      .articles-list .articles .cover {
        width: 300px;
        height: 300px; }
        .articles-list .articles .cover .article-cover {
          min-width: 600px; }
      .articles-list .articles .article-cover {
        min-height: 300px; }
      .articles-list .articles .info {
        font-size: 1.5rem; }
        .articles-list .articles .info a .title {
          font-size: 2rem; }
        .articles-list .articles .info .author-wrapper {
          margin: 10px 0; }
          .articles-list .articles .info .author-wrapper .profile-picture {
            width: 40px;
            height: 40px; }
          .articles-list .articles .info .author-wrapper .author {
            font-size: 1.5rem; }
        .articles-list .articles .info .text-wrapper {
          height: 110px;
          overflow: hidden;
          margin: 30px 0; }
          .articles-list .articles .info .text-wrapper .text {
            white-space: normal; }
        .articles-list .articles .info .published-date {
          font-size: 1.5rem; } }

@media only screen and (min-width: 1000px) {
  .articles-list {
    width: 70%; }
    .articles-list .articles {
      padding: 15px !important;
      grid-template-columns: 350px auto;
      padding: 0 15px; }
      .articles-list .articles .cover {
        width: 350px;
        height: 350px; }
        .articles-list .articles .cover .article-cover {
          min-width: 700px; }
      .articles-list .articles .info a .title {
        font-size: 3rem; } }

.article-detail {
  display: grid;
  grid-gap: 20px;
  justify-content: center;
  justify-items: center;
  padding: 15px; }
  .article-detail .title {
    font-size: 2rem;
    text-align: center;
    margin: 5px 0; }
  .article-detail .article-cover {
    border-radius: 5%;
    overflow: hidden; }
  .article-detail .author-wrapper {
    display: grid;
    justify-self: stretch;
    align-items: center;
    grid-template-columns: 1fr 5fr 1fr;
    grid-gap: 10px; }
    .article-detail .author-wrapper .profile-picture {
      min-width: 60px;
      width: 60px;
      height: 60px;
      border-radius: 50%; }
    .article-detail .author-wrapper .author {
      font-size: 1rem; }
    .article-detail .author-wrapper .published-date {
      margin: 0; }
    .article-detail .author-wrapper .fa-heart {
      justify-self: end;
      color: #570303; }
  .article-detail .text {
    text-align: justify;
    font-size: 1.5rem; }
  .article-detail .photo-author {
    text-align: end;
    font-size: 0.8rem; }
  @media only screen and (min-width: 750px) {
    .article-detail {
      width: 80%;
      margin: 90px auto; } }
  @media only screen and (min-width: 1000px) {
    .article-detail {
      width: 70%; }
      .article-detail .author-wrapper .author {
        font-size: 1.5rem; } }

.comments-list-title {
  text-align: center;
  margin: 0;
  color: #570303;
  font-size: 2rem; }
  .comments-list-title .fas {
    position: absolute;
    right: 10%; }

.comments-list {
  display: grid;
  grid-gap: 2px;
  justify-content: stretch;
  justify-items: center;
  padding: 15px; }
  .comments-list .comments {
    padding: 15px;
    width: 80%;
    background: #B8DABE;
    border-radius: 15px; }
    .comments-list .comments .fullname {
      font-size: 1.5rem;
      color: #570303; }
    .comments-list .comments .email {
      font-size: 0.8rem; }
    .comments-list .comments .comment-text {
      margin: 20px 0; }
    .comments-list .comments .added {
      text-align: right; }
    @media only screen and (min-width: 750px) {
      .comments-list .comments .fullname {
        font-size: 2rem; }
      .comments-list .comments .email {
        font-size: 1rem; }
      .comments-list .comments .comment-text {
        font-size: 1.5rem; }
      .comments-list .comments .added {
        font-size: 1.5rem; } }

.modal {
  display: none; }

.comments-form {
  display: grid;
  justify-items: center;
  grid-gap: 2px;
  justify-content: stretch;
  padding: 15px; }
  .comments-form label {
    display: none; }
  .comments-form .input {
    padding: 0.8rem;
    letter-spacing: normal;
    border: 0px solid;
    border-bottom: 0.5px solid #B9C1BC;
    width: 95%;
    font-size: 1.5rem; }
  .comments-form .submit-button {
    display: none;
    border: 0px solid;
    background-color: #B8DABE;
    color: #570303;
    font-size: 1.5rem;
    margin: 10px;
    border-radius: 15px;
    max-width: 250px; }
  .comments-form .error {
    border: 1px solid #570303; }
  @media only screen and (min-width: 750px) {
    .comments-form .input {
      width: 50%; } }

.hidden {
  display: grid; }

